/* bitter-regular - latin */
@font-face {
    font-family: 'Bitter';
    font-style: normal;
    font-weight: 400;
    src: url('../assets/fonts/bitter-v26-latin/bitter-v26-latin-regular.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('../assets/fonts/bitter-v26-latin/bitter-v26-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../assets/fonts/bitter-v26-latin/bitter-v26-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('../assets/fonts/bitter-v26-latin/bitter-v26-latin-regular.woff') format('woff'), /* Modern Browsers */
         url('../assets/fonts/bitter-v26-latin/bitter-v26-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../assets/fonts/bitter-v26-latin/bitter-v26-latin-regular.svg#Bitter') format('svg'); /* Legacy iOS */
  }
